@import "assets/styles/variables";
.container {
  margin-top: 40px;
  display: flex;
}

.item {
  position: relative;
  height: 41px;
  width: 41px;
  border: 1px solid $pagination-color;
  margin-right: -1px;
  color: $pagination-color;

  &:first-child {
    z-index: 1;
    border-top-left-radius: $rounded-md;
    border-bottom-left-radius: $rounded-md;
  }

  &:last-child {
    z-index: 1;
    border-top-right-radius: $rounded-md;
    border-bottom-right-radius: $rounded-md;
  }
}

.btn {
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  border: none;
  background-color: $pagination-background;
  font-size: 18px;
  font-weight: 500;
  color: inherit;

  &:hover {
    background-color: $pagination-hover;
  }
}
.active,
.active > .btn {
  color: $pagination-background;
  border-color: $pagination-color;
  background: $pagination-color;
}

.disabled {
  border-color: $transparent;
  color: $transparent;
  pointer-events: none;
}

.chevron_flip {
  transform: rotate(180deg);
}

@media screen and (max-width: $screen-desktop-sm) {
  .container {
    margin-top: 35px;
  }
}

@media screen and (max-width: $screen-tablet) {
  .container {
    margin-top: 30px;
  }
}
